.menu-button {
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 80px;
    padding: 36px 0;
    background-color: $main-c;
    box-shadow: 0 -10px 50px rgba(0, 0, 0, 0.12);
    z-index: 102;

    @include before__768 {
        display: none;
    }
}
